:root {
    color-scheme: dark;
    /* colors: */
    --main: hsl(50, 100%, 42%);
    --main-2: hsl(60, 80%, 32%);

    --text-color: hsl(0, 0%, 90%);
    --secondary-text-color: hsl(0, 0%, 70%);

    --bg: hsl(0, 0%, 0%);
    --secondary-bg: hsl(0, 0%, 9%);
    --tertiary-bg: hsl(0, 0%, 18%);

    --contrast: hsl(0, 90%, 48%);
    --contrast-2: hsl(0, 90%, 62%);
}
